@use "sass:math";

// インナー幅設定
// コンテンツのインナー幅（最も共通の幅が多い部分）
$inner: 1080px;
// ※キュンパス幅ではなくインナーの幅を指す

// 余白
$padding-pc: 25px;
$padding-sp: 15px;

////////////////////
// フォント設定
////////////////////

$base-font-family: "Noto Sans", sans-serif;
$second-font-family: "Noto Serif JP", serif;
$third-font-family: "Libre Caslon Text", serif;
// ※検証ツールでしっかり反映されているか確認する

body {
  font-family: $base-font-family;
}

// フォントウェイト
$regular: 400;
$medium: 500;
$bold: 700;

////////////////////
// 色の指定
////////////////////

//色指定（共通性のあるものは出来る限り指定すると良い）
$blue-black: #0D2936;
$blue: #294974;
$white: #fff;
$gray: #F9F8F6;
$gray2: #666;
$beige: #F4EDE3;
$gold: #D5AF65;
$brown: #7D5C1E;
$brown2: #503C28;
$red: #D64550;





