.hamburger {
  position: relative;
  width: 2.125rem;
  height: 2.0625rem;
  z-index: 9999;
}

.hamburger.is-active {
  position: absolute;
  right: 1.0625rem;
}

.hamburger span {
  position: absolute;
  width: 2.125rem;
  top: 0;
  right: 0;
  left: 0;
  height: 0.1875rem;
  background-color: #D5AF65;
  transition: 0.3s;
  border-radius: 0.1875rem;
}

.hamburger span:nth-child(1) {
  top: 15%;
}

.hamburger span:nth-child(2) {
  top: 50%;
}

.hamburger span:nth-child(3) {
  top: 85%;
}

.hamburger.is-active span:nth-child(1) {
  top: 50%;
  transform: rotate(38deg);
